const getRandomReward = (): Reward => {
  // T√≠nh t·ªïng x√°c su·∫•t
  const totalProbability = rewards.reduce((sum, reward) => sum + reward.probability, 0);
  
  // T·∫°o s·ªë ng·∫´u nhi√™n t·ª´ 0 ƒë·∫øn t·ªïng x√°c su·∫•t
  let random = Math.random() * totalProbability;
  
  // T√¨m ph·∫ßn th∆∞·ªüng d·ª±a tr√™n x√°c su·∫•t
  for (const reward of rewards) {
    random -= reward.probability;
    if (random <= 0) {
      return reward;
    }
  }
  
  // Tr·∫£ v·ªÅ ph·∫ßn th∆∞·ªüng ƒë·∫ßu ti√™n n·∫øu c√≥ l·ªói
  return rewards[0];
};

const App: React.FC = () => {
  const [gameState, setGameState] = useState<'playing' | 'completed'>('playing');
  const [score, setScore] = useState(0);
  const [earnedReward, setEarnedReward] = useState<Reward | null>(null);

  const handleGameComplete = (finalScore: number) => {
    setScore(finalScore);
    
    // Ki·ªÉm tra xem ng∆∞·ªùi ch∆°i c√≥ tr·∫£ l·ªùi ƒë√∫ng h·∫øt kh√¥ng
    // ƒêi·ªÉm c∆° b·∫£n cho m·ªói c√¢u h·ªèi l√† 100
    // ƒêi·ªÉm th·ªùi gian cho m·ªói c√¢u l√† t·ªëi ƒëa 30 * 3 = 90
    // T·ªïng ƒëi·ªÉm t·ªëi thi·ªÉu khi tr·∫£ l·ªùi ƒë√∫ng h·∫øt = s·ªë c√¢u h·ªèi * 100
    const minimumPerfectScore = questions.length * 100;
    
    // N·∫øu ƒëi·ªÉm s·ªë >= ƒëi·ªÉm t·ªëi thi·ªÉu khi tr·∫£ l·ªùi ƒë√∫ng h·∫øt
    // nghƒ©a l√† ng∆∞·ªùi ch∆°i ƒë√£ tr·∫£ l·ªùi ƒë√∫ng t·∫•t c·∫£ c√°c c√¢u h·ªèi
    if (finalScore >= minimumPerfectScore) {
      setEarnedReward(getRandomReward());
    }
    
    setGameState('completed');
  };

  const handlePlayAgain = () => {
    setScore(0);
    setEarnedReward(null);
    setGameState('playing');
  };

  return (
    <div>
      {gameState === 'playing' ? (
        <QuizGame
          questions={questions}
          onGameComplete={handleGameComplete}
        />
      ) : (
        <ResultScreen
          score={score}
          reward={earnedReward}
          onPlayAgain={handlePlayAgain}
        />
      )}
    </div>
  );
};

export default App;